body {
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f0f0f0;
}

#video-container {
    display: grid;
    grid-template-columns: repeat(2, 600px); /* 横に2列 */
    grid-template-rows: repeat(2, 400px); /* 縦に2列 */
    gap: 10px; /* グリッド間の間隔 */
    width: 1210px; /* グリッドの総幅 */
    height: 810px; /* グリッドの総高さ */
    margin: auto;
}

.video-frame {
    border: 1px solid #000; /* 枠線 */
    display: flex;
    justify-content: center;
    align-items: center;
}

video {
    width: 100%; /* 親要素に合わせる */
    height: 100%; /* 親要素に合わせる */
}
